<template>
  <div id="dashboard">
      <h1>
          Dashboard
      </h1>
      <table class="table table-striped table-inverse table-responsive mt-4">
          <thead class="thead-inverse">
              <tr>
                  <th>ID</th>
                  <th>Name</th>
                  <th>Department</th>
                  <th>Position</th>
                  <th><i class="fas fa-bolt fa-sm fa-fw"></i></th>
              </tr>
              </thead>
              <tbody>
                  <tr v-for="employee in employees" v-bind:key="employee.id">
                      <td scope="row">
                          {{ employee.id }}
                      </td>
                      <td>
                          {{ employee.emp_name }}
                      </td> 
                      <td>
                        {{  employee.emp_dep }}
                      </td>
                      <td>
                          {{ employee.emp_pos }}
                      </td>
                      <td>
                          <router-link :to="{name:'view-employee', params:{employee_id: employee.emp_id}}" class="">
                              <i class="fas fa-eye fa-lg fa-fw"></i>
                          </router-link>
                      </td>
                  </tr>
              </tbody>
      </table>
  </div>
</template>

<script>

import db from './firebaseInit'
export default {
    name: 'dashboard',
    data() {
        return {
            employees: []
        }
       }, 
       created() {
           db.collection('emp').orderBy('emp_dep').get().then(QuerySnapshot => {
               QuerySnapshot.forEach(doc => {
                   const data = { 
                       'id': doc.id,
                       'emp_id': doc.data().emp_id,
                       'emp_name': doc.data().emp_name,
                       'emp_dep': doc.data().emp_dep,
                       'emp_pos': doc.data().emp_pos
                       }
                    this.employees.push(data)
               });
               });
       } 
}
</script>

