<template>
  <div id="newEmployee">
    <h1>New Employee</h1>
    <form class="row" @submit.prevent="submitNewEmployee()">
      <div class="form-group col-8">
        <label for="emp_name">Name</label>
        <input
          type="text"
          v-model="emp_name"
          name="emp_name"
          id="emp_name"
          class="form-control"
          placeholder="Enter employee name"
          aria-describedby="helpId"
          required
        />
      </div>
      <div class="form-group col-8">
        <label for="emp_name">ID</label>
        <input
          type="text"
          v-model="emp_id"
          name="emp_id"
          id="emp_id"
          class="form-control"
          placeholder="Enter employee ID"
          aria-describedby="helpId"
          required
        />
      </div>
      <div class="form-group col-8 mt-3">
        <label for="emp_dep">Department</label>
        <input
          type="text"
          v-model="emp_dep"
          name="emp_dep"
          id="emp_dep"
          class="form-control"
          placeholder="Enter employee department"
          aria-describedby="helpId"
          required
        />
      </div>
      <div class="form-group col-8 mt-3">
        <label for="emp_pos">Position</label>
        <input
          type="text"
          name="emp_pos"
          v-model="emp_pos"
          id="emp_pos"
          class="form-control"
          placeholder="Enter employee Position"
          aria-describedby="helpId"
          required
        />
      </div>
      <button
        type="submit"
        class="btn btn-primary btn-lg btn-block mt-3 col-6"
      >
        Submit
      </button>
    </form>
  </div>
</template>

<script>
import db from "./firebaseInit";
export default {
  name: "newEmployee",
  data() {
    return {
      emp_id: null,
      emp_name: null,
      emp_pos: null,
      emp_dep: null,
    };
  },
  methods: {
    submitNewEmployee() {
      db.collection("emp").add({
        emp_id: this.emp_id,
        emp_name: this.emp_name,
        emp_dep: this.emp_dep,
        emp_pos: this.emp_pos
      }).then(
          this.$router.push('/dashboard')
      ).catch(error => console(error));
    },
  },
};
</script>

