<template>
  <div id="editEmployee">
    <h1>edit Employee</h1>
    <form class="row" @submit.prevent="updateEmployee()">
      <div class="form-group col-8">
        <label for="emp_name">Name</label> {{ emp_name }}
        <input
          type="text"
          v-model="emp_name"
          name="emp_name"
          id="emp_name"
          class="form-control"
          placeholder="Enter employee name"
          aria-describedby="helpId"
          required
        />
      </div>
      <div class="form-group col-8">
        <label for="emp_name">ID</label>
        <input
          type="text"
          v-model="emp_id"
          name="emp_id"
          id="emp_id"
          class="form-control"
          placeholder="Enter employee ID"
          aria-describedby="helpId" disabled
          required
        />
      </div>
      <div class="form-group col-8 mt-3">
        <label for="emp_dep">Department</label>
        <input
          type="text"
          v-model="emp_dep"
          name="emp_dep"
          id="emp_dep"
          class="form-control"
          placeholder="Enter employee department"
          aria-describedby="helpId"
          required
        />
      </div>
      <div class="form-group col-8 mt-3">
        <label for="emp_pos">Position</label>
        <input
          type="text"
          name="emp_pos"
          v-model="emp_pos"
          id="emp_pos"
          class="form-control"
          placeholder="Enter employee Position"
          aria-describedby="helpId"
          required
        />
      </div>
      <button type="submit" class="btn btn-primary btn-lg btn-block mt-3 col-6">
        Edit
      </button>
    </form>
  </div>
</template>

<script>
import db from "./firebaseInit";
export default {
  name: "editEmployee",
  data() {
    return {
      emp_name: null,
      emp_id: null,
      emp_pos: null,
      emp_dep: null,
    };
  },
    beforeRouteEnter(to, from, next) {
    // ...
    db.collection("emp")
      .where("emp_id", "==", to.params.employee_id)
      .get()
      .then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          next((vm) => {
            vm.emp_id = doc.data().emp_id;
            vm.emp_name = doc.data().emp_name;
            vm.emp_dep = doc.data().emp_dep;
            vm.emp_pos = doc.data().emp_pos;
          });
        });
      });
  },
  watch: {
    $route: "fetchData",
  },
  methods: {
    fetchData() {
      db.collection("emp")
        .where("emp_id", "==", this.$route.params.employee_id)
        .get()
        .then((querySnapshot) => {
          querySnapshot.forEach((doc) => {
            this.emp_id = doc.data().emp_id;
            this.emp_name = doc.data().emp_name;
            this.emp_dep = doc.data().emp_dep;
            this.emp_pos = doc.data().emp_pos;
          });
        });
    },
    updateEmployee() {
      db.collection("emp")
        .where("emp_id", "==", this.$route.params.employee_id)
        .get()
        .then((querySnapshot) => {
          querySnapshot.forEach(doc => {
              doc.ref.update({
                emp_id: this.emp_id,
                emp_name: this.emp_name,
                emp_dep: this.emp_dep,
                emp_pos: this.emp_pos
              }).then(() => {
                  this.$router.push({name:'view-employee', params: {employee_id: this.emp_id}});
              });
          });
          
        });
    }
  },
};
</script>

