<template>
  <div id="viewEmployee">
    <h1 class="bg-success text-white mt-3 p-4 w-100">View Employee</h1>
    <div>
      <div class="list-group">
        <li
          class="
            list-group-item list-group-item-action
            flex-column
            align-items-start
            active
          "
        >
          <div class="d-flex w-100 justify-content-between">
            <h5 class="mb-1">{{ emp_name }}</h5>
            <small>{{ emp_pos }}</small>
          </div>
          <p class="mb-1">Department: {{ emp_dep }}</p>
          <small>Position: {{ emp_pos }}</small>
        </li>
        <div class="btn-group" role="group" aria-label="">
          <router-link to="/" class="btn btn-secondary">Back</router-link>
          <router-link v-bind:to="{name: 'edit-employee', params: {emp_id: employee_id}}"
           class="btn btn-success">
           <i class="fas fa-pencil-alt fa-lg fa-fw"></i>
           </router-link>
          <button @click="deleteEmp()" class="btn btn-outline-danger">
            Delete
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import db from "./firebaseInit";
export default {
  name: "viewEmployee",
  data() {
    return {
      emp_id: null,
      emp_name: null,
      emp_pos: null,
      emp_dep: null,
      employee: {},
      employee_id: null
    };
  },
  beforeRouteEnter(to, from, next) {
    // ...
    db.collection("emp")
      .where("emp_id", "==", to.params.employee_id)
      .get()
      .then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          next((vm) => {
            vm.emp_id = doc.data().emp_id;
            vm.emp_name = doc.data().emp_name;
            vm.emp_dep = doc.data().emp_dep;
            vm.emp_pos = doc.data().emp_pos;
          });
        });
      });
  },
  watch: {
    $route: "fetchData",
  },
  methods: {
    fetchData() {
      db.collection("emp")
        .where("emp_id", "==", this.$route.params.employee_id)
        .get()
        .then((querySnapshot) => {
          querySnapshot.forEach((doc) => {
            this.emp_id = doc.data().emp_id;
            this.emp_name = doc.data().emp_name;
            this.emp_dep = doc.data().emp_dep;
            this.emp_pos = doc.data().emp_pos;
          });
        });
    },
    deleteEmp() {
      if (confirm("Are you sure")) {
        db.collection("emp")
          .where("emp_id", "==", this.$route.params.employee_id)
          .get()
          .then((querySnapshot) => {
            querySnapshot.forEach((doc) => {
              doc.ref.delete();
              this.$router.push('/dashboard');
            });
          });
      }
    }
  }
};
</script>

